import MTCDateText from "./date_text"

export default function MTCDateTimeText(props) {
  return (
    <MTCDateText
      {...props}
      custom={{
        more: {
          format: "YYYY-MM-DD hh:mm:ss",
          dateTextFormat: "YYYY-MM-DD hh:mm:ss",
        },
      }}
    />
  )
}
